import React, { useState } from "react";
import LayoutKom from "../pages/Layout";
import { motion, AnimatePresence } from "framer-motion";
import { NavLink } from "react-router-dom";
import { TypeAnimation } from "react-type-animation";

import Card2 from "../components/Card2";

import Orang1 from "../assets/Logo2.png";
import Gambar2 from "../assets/Background3.jpg";
import Emas1 from "../assets/emas1.jpg";

const KomHome = () => {
  return (
    <section className="">
      <LayoutKom>
        <div className="px-4 z-10 relative w-full">
          <div className="z-10">
            <div className="pb-20"></div>

            <div className="grid lg:grid-cols-2  grid-cols-1 gap-3 md:p-10 ">
              <div>
                <blockquote className="text-warna5 ">
                  <h1 className="pt-16 md:text-6xl text-2xl self-center font-tema1 font-semibold ">
                    Hallo! Selamat datang di profil saya!{" "}
                  </h1>

                  <div className="lg:text-xl text-sm">
                    <p className="pt-3 self-center font-utama1 ">
                      "Sebagai seorang Website Developer yang mencintai bidang
                      Desain Website. Di sini Anda akan menemukan cerita di
                      balik setiap karya. Terima kasih sudah berkunjung, dan
                      saya berharap Anda mendapatkan sesuatu pada profile saya"
                    </p>
                    <div className="flex items-center">
                      <img src={Orang1} alt="" className="h-20 p-4" />
                      <NavLink to="/about">
                        <button
                          style={{ backdropFilter: "blur(20px)" }}
                          className="text-black bg-white hover:bg-yellow-600 focus:ring-4 focus:outline-none focus:ring-blue-300 
     font-medium rounded-lg text-sm px-5 py-2.5 text-center border font-tema3"
                        >
                          <motion.div
                            className="inline-block" // Tambahkan class inline-block di sini
                            initial={{ opacity: 0, scale: 0.5 }}
                            animate={{ opacity: 1, scale: 1 }}
                            transition={{ duration: 0.5 }}
                          >
                            <p>
                              {" "}
                              -
                              <TypeAnimation
                                sequence={[
                                  "Cek Tentang Saya",
                                  1000,
                                  "Check About Me",
                                  1000,
                                  "تحقق عني",
                                  1000,
                                  "Mira Sobre Mi",
                                  1000,
                                  "检查我的情况",
                                  1000,
                                ]}
                                wrapper="span"
                                speed={30}
                                repeat={Infinity}
                              />
                            </p>
                          </motion.div>
                        </button>
                      </NavLink>
                    </div>
                  </div>
                </blockquote>
                <div className="bg-gradient-to-tr from-warna1 to-warna5 md:p-10 p-2 rounded-md relative">
                  <img
                    className="rounded-md md:w-full md:h-full object-cover"
                    src={Gambar2}
                    alt=""
                  />
                  <div className="absolute inset-0 flex items-center justify-center p-5">
                    <p className="text-warna5 text-center text-xs md:text-lg font-utama1  bg-opacity-50 p-4 rounded-md">
                      "Sebagai seorang Website Developer yang mencintai bidang
                      Desain Website. Di sini Anda akan menemukan cerita di
                      balik setiap karya. Terima kasih sudah berkunjung, dan
                      saya berharap Anda mendapatkan sesuatu pada profil saya"
                    </p>
                  </div>
                </div>
              </div>
              <div>
                <img className="hidden lg:block min-w-60" src={Emas1} alt="" />
              </div>
            </div>

            <div>
              <Card2 />
            </div>
            <div className="pb-5" />
          </div>
        </div>
      </LayoutKom>
    </section>
  );
};

export default KomHome;
