import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

import Gambar from "../assets/logo_baju.png";

import { BiUndo } from "react-icons/bi";

const CardAbout = () => {
  const [selectedId, setSelectedId] = useState(null);
  const items = [
    {
      id: 1,
      subtitle: "Terhadap Website Developer",
      warna: "bg-warnacampur1",
      isi: "Sebagai pengembang situs web, saya mengikuti perkembangan industri terkini. Dengan Melihat tren baru yang muncul dalam proses pengembangan, terdapat kebutuhan berkelanjutan untuk meningkatkan keterampilan dan kemampuan dalam menganalisis kebutuhan. Dan sebagai seorang yang mendapatkan pelatihan SEO saya mampu mengoptimalkan dengan efektif ditambah dukungan akan pelatihan saya tentang data Science dan bisa memberikan Analisa maupun prediksi dalam mengolah data",
      title: "Visi ",
      image: Gambar,
    },
    {
      id: 2,
      subtitle: "Meenjadi seorang Website Developer",
      warna: "bg-warnacampur7",
      isi: "Pada saat ini dalam mencapai kemampuan yang kompatibel dalam segala aspek saya juga mengikuti dan mempelajari teknik SEO agar Website lebih efektif dan juga menggunakan pendekatan Data Science agar bisa melihat peluang dengan data yang akurat sebab dalam dunia digital website bagus belum tentu bisa memberikan daya tarik dan semuanya harus berkesinambungan antara konten, trend, dan bahkan kesukaan masyarakat tertentu  ",
      title: "Misi ",
      image: Gambar,
    },
    {
      id: 3,
      title: "Pendidikan",
      warna: "bg-warnacampur3",
      subtitle: "Pemahaman & Pengalaman",
    },
    {
      id: 4,
      subtitle: "Keindahan dan daya Tarik",
      title: "Hobi",
      warna: "bg-warnacampur2",
    },
    {
      id: 5,
      subtitle: "Kemampuan Hingga persaingan",
      title: "Skill",
      warna: "bg-warnacampur4",
    },
  ];
  return (
    <div className="pt-20">
      <motion.div className="flex items-center justify-center pt-20">
        <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 p-2 gap-4">
          {items.map((item) => (
            <motion.div
              className={`card ${
                item.warna
              } bg-opacity-80 rounded-lg shadow-md cursor-pointer transform transition-transform duration-500 hover:scale-105 ${
                selectedId === item.id ? "card-selected" : ""
              }`}
              layoutId={`card-container-${item.id}`}
              onClick={() => setSelectedId(item.id)}
              key={item.id}
              initial={{ scale: 1 }}
              animate={{ scale: selectedId === item.id ? 1.1 : 1 }} // Increase scale on selected card
              transition={{ duration: 0.3 }}
            >
              <div className="card-content p-4">
                <motion.h2 className="text-xl font-bold mb-2 text-black font-tema3">
                  {item.title}
                </motion.h2>
                <motion.h5 className="text-sm font-bold mb-1 text-gray-700 font-tema2">
                  {item.subtitle}
                </motion.h5>
              </div>
            </motion.div>
          ))}
        </div>

        <AnimatePresence>
          {selectedId && (
            <motion.div
              className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center pt-20"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
            >
              {items.map(
                (item) =>
                  item.id === selectedId && (
                    <motion.div
                      className={`${item.warna} rounded-lg p-4 shadow-md max-w-lg mx-auto`}
                      layoutId={`card-container-${item.id}`}
                      key={item.id}
                      initial={{ scale: 0.8, opacity: 0 }}
                      animate={{ scale: 1, opacity: 1 }}
                      exit={{ scale: 0.8, opacity: 0 }}
                    >
                      <motion.div className="relative">
                        <motion.button
                          className="absolute right-2 py-1 px-2 text-center text-white bg-warna5 rounded-xl mb-2"
                          onClick={() => setSelectedId("")}
                        >
                          <BiUndo />
                        </motion.button>
                        <motion.h2 className="text-xl font-tema3 font-bold mb-2 text-black">
                          {item.title}
                        </motion.h2>
                        <motion.h5 className="text-sm font-bold font-tema2 mb-1 text-gray-700">
                          {item.subtitle}
                        </motion.h5>
                        <img src={item.image} className="card-image w-20" />
                        <motion.p
                          className="text-md text-gray-700"
                          initial={{ opacity: 0 }}
                          animate={{ opacity: 1 }}
                          exit={{ opacity: 0 }}
                        >
                          {item.isi}
                        </motion.p>
                      </motion.div>
                    </motion.div>
                  )
              )}
            </motion.div>
          )}
        </AnimatePresence>
      </motion.div>
    </div>
  );
};

export default CardAbout;
